import React from 'react'
import { useSelector } from 'react-redux'
import { useParams } from 'react-router-dom';
import "./taikhoan.css"
function Chitiettaikhoan(props) {
    const { id } = useParams();
    const users = useSelector(state => state.taikhoan.user.data);
    const user = []
    if (users) {
        for (let i = 0; i < users.length; i++) {
            if (users[i].id === +id) {
                user.push(users[i])
            }
        }
    }
    console.log(user);

    return (
        <div id="admin">
            <div className="heading">
                <h4>Chi tit ti khon</h4>
                <div className="hr"></div>
            </div>
            <div className="content">
                <div className="ct">
                    {!user ? 's' :
                        user.map(ok => (
                            <div key={ok.id}>
                                <div className="text-center"><img src={ok.avatar ? ok.avatar : 'data:image/jpeg;base64,'} className="avatar-ct" alt="" /></div>
                                <p>Tn ngi dng:&emsp; <b><i>{ok.name}</i></b></p>
                                <p>Email:&emsp; <b><i>{ok.email}</i></b></p>
                                <p>S in thoi:&emsp; <b><i></i>{ok.sdt}</b></p>
                                <p>Gii tnh:&emsp; <b><i>{ok.gioitinh === 1 ? "Nam" : "N"}</i></b></p>
                                <p>Ngy sinh:&emsp; <b><i>{ok.ngaysinh}</i></b></p>
                                <p>a ch:&emsp; <b><i>{ok.diachi}</i></b></p>
                                <p>Website:&emsp; <b><i>Lai.com.vn</i></b></p>
                                <p>Chm ngn:</p>
                                <div className="container">
                                    <p>C lm th mi c n</p>
                                </div>
                                <p>Chc v:&emsp; <b><i>Kim duyt</i></b></p>
                                <p>B phn:&emsp; <b><i>Kim duyt</i></b></p>
                                <p>Github:&emsp; <b><i>Kim duyt</i></b></p>
                                <p>Facebook:&emsp; <b><i>Kim duyt</i></b></p>
                                <p>K nng:</p>
                                <div className="container">
                                    <p>C / C + +, data structures, software engineering, operating systems, computer networks, databases, compiler theory, computer architecture, Microcomputer Principle and Interface Technology, Computer English, Java, ASP, etc.</p>
                                </div>
                            </div>
                        ))
                    }
                </div>
            </div>
        </div>

    )
}

Chitiettaikhoan.propTypes = {

}

export default Chitiettaikhoan