import React from 'react'
import { Tabs } from 'antd';
import { StickyContainer, Sticky } from 'react-sticky';
import 'antd/dist/antd.css';
import Danhgia from '../danhgia/Danhgia';
import Chitiettour from '../chitiettour/Chitiettour';
import Dichvudikem from '../dichvudikem/Dichvudikem';
import Luuy from '../luuy/Luuy';
import Maps from './../map/Maps'
import Chitietgia from '../gia/Chitietgia';
import "./detail.css"
function Detail(props) {
    const { TabPane } = Tabs;
    const renderTabBar = (props, DefaultTabBar) => (
        <Sticky bottomOffset={80}>
            {({ style }) => (
                <DefaultTabBar {...props} className="site-custom-tab-bar" style={{ ...style }} />
            )}
        </Sticky>
    );
    return (
        <div className="mb-5 ">
            <StickyContainer>
                <Tabs defaultActiveKey="1" renderTabBar={renderTabBar}>
                    <TabPane tab="Chi tit tour" key="1" >
                        <Chitiettour id={props.id} />
                    </TabPane>
                    <TabPane tab="Gi" key="2">
                        <Chitietgia id={props.id} />
                    </TabPane>
                    <TabPane tab="Dch v i km" key="3">
                        <Dichvudikem id={props.id} />
                    </TabPane>
                    <TabPane tab="Lu " key="4">
                        <Luuy id={props.id} />
                    </TabPane>
                    <TabPane tab="Bn " key="5">
                        <div className="container"><Maps id={props.id} /></div>
                    </TabPane>
                    <TabPane tab="nh gi khch hng" key="6">
                        <Danhgia id={props.id} />
                    </TabPane>
                </Tabs>
            </StickyContainer>
        </div>
    )
}

Detail.propTypes = {

}

export default Detail
