import { message } from "antd";
import axiosClient from "./axiosClient";

class ChitieuApi {
    getAll = (params) => {
        const url = '/chitieus';
        return axiosClient.get(url, { params });
    };
    postchitieu = (params) => {
        const url = '/chitieus';
        return axiosClient.post(url, params).then(data => {
            message.success("Thm thnh cng!");
        }).catch(err => {
            message.error("C li xy ra!");
        });
    };
    deletechitieu = (id) => {
        const url = `/chitieus/${id}`;
        return axiosClient.delete(url).then(data => {
            message.success("Xo thnh cng!");
        }).catch(err => {
            message.error("C li xy ra!");
        });
    };
    editchitieu = (params) => {
        const url = `/chitieus/${params.idsua}`;
        return axiosClient.patch(url, params).then(data => {
            message.success("Sa thnh cng!");
        }).catch(err => {
            message.error("C li xy ra!");
        });
    }
}
const chitieuApi = new ChitieuApi();
export default chitieuApi;