import { message } from "antd";
import axiosClient from "./axiosClient";

class UserroleApi {
    getAll = async (params) => {
        const url = '/userroles';
        return await axiosClient.get(url, { params });
    };
    postuserrole = (params) => {
        const url = '/userroles';
        return axiosClient.post(url, params).then(data => {
            message.success("Thm thnh cng!");
        }).catch(err => {
            message.error("C li xy ra!");
        });
    };
    deleteuserrole = (id) => {
        const url = `/userroles/${id}`;
        return axiosClient.delete(url).then(data => {
            message.success("Xo thnh cng!");
        }).catch(err => {
            message.error("C li xy ra!");
        });
    };
    edituserrole = (params) => {
        const url = `/userroles/${params.idsua}`;
        console.log(params);
        return axiosClient.patch(url, params).then(data => {
            message.success("Cp quyn thnh cng!");
        }).catch(err => {
            message.error("C li xy ra!");
        });
    }
    edituserroleHeader = (params) => {
        const url = `/userroles/${params.idsua}`;
        console.log(params);
        return axiosClient.patch(url, params).then(data => {
            return data.data
        }).catch(err => {
            message.error("C li xy ra!");
        });
    }
    getuserrole = () => {
        const url = '/userroles';
        return axiosClient.get(url).then(data => {
            return data.data
        });
    };
}
const userroleApi = new UserroleApi();
export default userroleApi;